@import '~@microsoft/sp-office-ui-fabric-core/dist/sass/SPFabricCore.scss';

.semTalkShapeStyle {
  /* layout */
  display: flex;
  .pivottab {
    padding-top: 20px;
  }
  .container {
    @include ms-Grid;
    display: inline-block;
  //  // position: relative;
  //   width: 100%;
  //   padding-bottom: 100%; /* aspect ratio */
  //   vertical-align: top;
    overflow: hidden;
   }
.textfield {
  width: 1000px;
}
   .row {
    @include ms-Grid-row;
  }

  .column {
    @include ms-Grid-col;
  }

  .title {
    @include ms-font-xl;
  }

  .subTitle {
    @include ms-font-l;
  }


  .form {
    @include ms-font-l;
  }

  .label {
    @include ms-font-l;
  }

  .description {
    font-weight: $ms-font-weight-light;
    font-size: $ms-font-size-s;
    margin: 0 4px;
    vertical-align: top;
  }

  .button {
    // Our button
    text-decoration: none;
    height: 32px;

    // Primary Button
    min-width: 80px;
    background-color: $ms-color-themePrimary;
    border-color: $ms-color-themePrimary;
    color: $ms-color-white;

    // Basic Button
    outline: transparent;
    position: relative;
    font-family: "Segoe UI WestEuropean", "Segoe UI", -apple-system, BlinkMacSystemFont, Roboto, "Helvetica Neue", sans-serif;
    -webkit-font-smoothing: antialiased;
    font-size: $ms-font-size-m;
    font-weight: $ms-font-weight-regular;
    border-width: 0;
    text-align: center;
    cursor: pointer;
    display: inline-block;
    padding: 0 16px;

    .label {
      font-weight: $ms-font-weight-semibold;
      font-size: $ms-font-size-m;
      height: 32px;
      line-height: 32px;
      margin: 0 4px;
      vertical-align: top;
      display: inline-block;
    }
  }
}
