@import '~@microsoft/sp-office-ui-fabric-core/dist/sass/SPFabricCore.scss';

.semTalkToolBar {
 // display: flex;
 // color: $ms-color-neutralPrimary;

  .container {
    margin: 0px auto;
  }

  .row {
    @include ms-Grid-row;
  }

  .column {
    @include ms-Grid-col;
  }

  .title {
    @include ms-font-xl;
  }

  .subTitle {
    @include ms-font-l;
  }

  .description {
    @include ms-font-l;
  }

  .button {
    border-width: 0 !important;
    outline: transparent;
    position: relative;
    text-align: center;
    cursor: pointer;
    display: inline-block;
    color: $ms-color-neutralPrimary;
  }
}
